.meal {
  display: flex;
  /* flex-direction: column; */
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  /* border-bottom: 1px solid #ccc; */
}

.meal h3 {
  margin: 0 0 0.25rem 0;
}

.description {
  font-style: italic;
}

.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: #004660;
  font-size: 1.25rem;
}
.meal_img {
  border-radius: 10px;
  margin-right: 1rem;
}
